import {AfterViewInit, Component, ComponentFactoryResolver, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {ActivatedRoute, ParamMap, Router} from '@angular/router';
import {MdDialog} from '@angular/material';
import {VideoViewComponent} from '../../share/video-view/video-view.component';
import {AdHostDirective} from '../ad-host.directive';
import 'rxjs/add/operator/switchMap';
import {WiSvg1Component} from '../wi-svg1/wi-svg1.component';
import {WiSvg2Component} from '../wi-svg2/wi-svg2.component';
import {Subscription} from 'rxjs/Subscription';
import {BaseWiSvgComponent} from "../BaseWiSvgComponent";

@Component({
    selector: 'app-dialog',
    templateUrl: './dialog.component.html',
    styleUrls: ['./dialog.component.sass'],
    entryComponents: [
        WiSvg1Component,
        WiSvg2Component]
})
export class HemComponent implements OnInit, AfterViewInit, OnDestroy {
    @ViewChild(AdHostDirective) adHost: AdHostDirective;
    private subscription: Subscription;

    ngAfterViewInit(): void {

    }

    ngOnDestroy(): void {
        this.subscription.unsubscribe();
    }

    constructor(private router: Router,
                private dialog: MdDialog,
                private activeRouter: ActivatedRoute,
                private componentFactoryResolver: ComponentFactoryResolver) {
    }

    front() {
        this.router.navigateByUrl('emi/cutting/cutting');
    }

    home() {
        this.router.navigateByUrl('emi/home');
    }

    ngOnInit() {
        const that = this;
        this.subscription = this.activeRouter.paramMap.map((params: ParamMap) =>
            params.get('id')).subscribe(_id => {
            console.log(that);
            if (_id === '1') {
                that.adHost.viewContainerRef.clear();
                const componentFactory = that.componentFactoryResolver.resolveComponentFactory(WiSvg1Component);
                console.log(componentFactory);
                const componentRef = that.adHost.viewContainerRef.createComponent(componentFactory);
                (<BaseWiSvgComponent>componentRef.instance).data = null;
            }
            if (_id === '2') {
                that.adHost.viewContainerRef.clear();
                const componentFactory2 = that.componentFactoryResolver.resolveComponentFactory(WiSvg2Component);
                console.log(componentFactory2);
                const componentRef2 = that.adHost.viewContainerRef.createComponent(componentFactory2);
                (<BaseWiSvgComponent>componentRef2.instance).data = null;
            }
        });
    }

    openDialog(url: string): void {
        const dialogRef = this.dialog.open(VideoViewComponent, {
            width: '360px',
            height: '240px',
            data: {url: url}
        });

        dialogRef.afterClosed().subscribe(result => {
            console.log('The dialog was closed');
            // this.animal = result;
        });
    }
}



