import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {RootComponent} from './root/root.component';
import {RouterModule, Routes} from '@angular/router';
import {MdButtonModule, MdCardModule, MdDialogModule, MdGridListModule, MdTooltipModule} from '@angular/material';
import {HemComponent} from './dialog/dialog.component';
import {FormsModule} from '@angular/forms';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {InfoComponent} from './info/info.component';
import {BackComponent} from './back/back.component';
import {FrontComponent} from './front/front.component';
import {BackViewComponent} from './back-view/back-view.component';
import {ShareModule} from '../share/share.module';
import {WiSvg1Component} from './wi-svg1/wi-svg1.component';
import {WiSvg2Component} from './wi-svg2/wi-svg2.component';
import { AdHostDirective } from './ad-host.directive';

const routes: Routes = [
    {path: 'emi/cutting/cutting', component: RootComponent},
    {path: 'emi/cutting/hem', component: HemComponent},
    {path: 'emi/cutting/bk', component: BackViewComponent},
];

@NgModule({
    imports: [
        RouterModule.forRoot(routes),
        CommonModule,
        BrowserAnimationsModule,

        ShareModule,

        MdCardModule,
        MdButtonModule,
        MdGridListModule,
        MdDialogModule,
        FormsModule,
        MdTooltipModule,
    ],
    exports: [RootComponent, HemComponent],
    entryComponents: [
        WiSvg1Component,
        WiSvg2Component
    ],
    declarations: [
        RootComponent,
        HemComponent,
        InfoComponent,
        BackComponent,
        FrontComponent,
        BackViewComponent,
        WiSvg1Component,
        WiSvg2Component,
        AdHostDirective
    ]
})
export class CuttingModule {
}



